.child-view {width: 100%;height: 100%;-webkit-transition: all .5s cubic-bezier(.55, 0, .1, 1);transition: all .5s cubic-bezier(.55, 0, .1, 1);}
.alert-enter-active, .alert-leave-active {-webkit-transition: all .6s;transition: all .6s;}
.alert-enter, .alert-leave-to{opacity: 0;}
