.bottom-enter-active, .bottom-leave-active {-webkit-transition: all .3s;transition: all .3s;-webkit-transform: translateY(0);transform: translateY(0); }
.bottom-enter, .bottom-leave-to{opacity: 0;-webkit-transform: translateY(100%);transform: translateY(100%);}
